.main {
  @extend %large-text;
}

.box {
  display: grid;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 50px;
  grid-template-columns: repeat( 4, 200px);
  gap: 48px;
}

.card {
  display: flex;
  flex-direction: column;
  transition-duration: 0.3s;
  align-items: center;
  border-radius: 5px;
  width: 200px;
  height: 408px;
  box-sizing: border-box;
  background: $white-color;
  border: 1px solid $border-color;

  &__img {
    width: 160px;
    height: 134px;
    margin: 32px 0 40px;
  }
  
  &__info {
    margin-bottom: 16px;
  }
  
  &__title {
    transition-duration: 0.3s;
    width: $width-content;
    height: 36px;
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    color: $darc-blue-color;
    cursor: pointer;
  }
  
  &__code {
    margin: 4px 0 0;
    width: 98px;
    height: 14px;
    font-weight: 400;
    font-size: 10px;
    line-height: 14px;
    color: $grey-color;
  }
  
  &__review {
    width: $width-content;
    @include display();
    align-items: center;
    color: $darc-blue-color;
  }
  
  &__review-rating {
    font-size: 10px;
    line-height: 14px;
  }
  
  &__price {
    margin: 24px 0 16px;
    width: $width-content;
    @include display();
    color: $darc-blue-color;
  }
  
  &__price-text {
    font-size: 12px;
    line-height: 18px;
    color: $grey-color;
  }
  
  &__price-price {
    font-weight: 700;
    font-size: 16px;
    line-height: 18px;
  }
  
  &__button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: $width-content;
    height: 40px;
    background: $color-primary;
    border-radius: 5px;
    text-decoration: none;
    font-weight: 700;
    font-size: 14px;
    line-height: 16px;
    color: $white-color;
  }
  
  &__button:hover {
    box-sizing: border-box;
    background: $white-color;
    color: $color-primary;
    border: 1px solid $color-primary;
  }

  &:hover {
    transform: scale(1.2);
    .card__title {
      color: $title-color;
    }
  }
}

@media screen and (min-width: 768px) and (max-width: 1023px) {
  .box {
    grid-template-columns: repeat( 3, 200px);
  }
}

@media screen and (min-width: 488px) and (max-width: 767px) {
  .box {
    grid-template-columns: repeat( 2, 200px);
  }
}

@media screen and (min-width: 0) and (max-width: 487px) {
  .box {
    grid-template-columns: repeat( 1, 200px);
  }
}
